  async presentAlert(title,msg) {
    const alert = await this.alertController.create({
      header: title,
      message: msg,
      backdropDismiss:false,
      buttons: [
        {
          text: 'Ok',
          handler: () => {
            window.location.reload();
            // this.authenticate.logout();
            // this.navCtrl.navigateForward('tabs/home');
          }
        }
      ],
    });

    await alert.present();
  }
